.winner{
    display: flex;
    flex-direction: column;
    width: 600px;
    align-items: center;
    justify-content: center;
    transform: translateY(-10rem);
    animation-name: win;
    animation-duration: 3s;
}
.anuncio{
    display: flex;
    flex-direction: row;
    width: 100%;
    justify-content: center;
}
.anuncio h1{
    margin-left: 20px;
    font-size: 60px;
}
.winner img{
    animation-name: win;
    animation-duration: 3s;
}
button{
    width: 200px;
    height: 50px;
    border: none;
    border-radius: 10px;
    font-size: 20px;
}
#pa{
    animation-name: win;
    animation-duration: 3s;
}
#dra{
    margin-left: 0px;
}

@keyframes win {
    0%{
        color: rgba(255, 255, 255, 0);
        background-color: rgba(255, 255, 255, 0);
        transform: translateY(10rem);
    }

}
