.cover{
    width: 5rem;
    height: 5rem;
    display: flex;
    align-items: center;
    justify-content: center;
}
.re{
    transform: scale(0.5) translateY(-10rem);
    animation-name: reduce;
    animation-duration: 3s;
}
@keyframes reduce {
    0%{
        transform: scale(1) translateY(0rem);
    }
}